.currentData {
    z-index: 3;
    padding: 0 20px 0 20px;
    margin: 20px;
    background-color: black;
    color: #00DDFF;
    text-shadow: 0 0 5px #00FFFF;
    border: 3px solid #FFFFFF;
    border-radius: 10px;
    -webkit-box-shadow: 0 0 10px #EEEEEE;
            box-shadow: 0 0 10px #EEEEEE;
    grid-column: 1/2;
    grid-row: 2/4;
    opacity: 0.8;
}



